@import "colors";

.home {
    width: 100%;
    height: 100vh;
    background-color: var(--second);
}

.home>main {
    position: absolute;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 100%;
    height: 100%;
    padding: 2rem;
    justify-content: center;
}

.home>main>h1 {
    color: var(--first);
}

.home>main>p {
    color: var(--first);
}

.home::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    background: no-repeat center/auto url('../assets/1.png');
    filter: blur(7px);
}

.home2 {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    padding: 500px 0;
    align-items: center;
    background-color: var(--second);
    margin-top: -300px ;
    margin-bottom: -300px;
} 

.home2>img {
    width: 30%;
    animation: imgUp 0.7s linear infinite alternate
}

@keyframes imgUp {
    to {
        transform: translateY(-10px);
    }
}

.home2>div {
    width: 50%;
    padding: 5rem;
}

.home2>div>p {
    color: var(--first);
    font: 100 1.2rem;
    letter-spacing: 2px;
    word-spacing: 6px;
}

.home3 {
    width: 100%;
    height: 120vh;
    background-color: var(--second);
}

.home3>div {
    width: 70%;
    height: 100vh;
    background-color: var(--first);
    border-radius: 0 200px 200px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.home3>div>h1 {
    text-transform: uppercase;
    border-bottom: 2px solid var(--dark);
    padding: 0.5rem;
    width: 15rem;
    color: var(--dark);
}

.home3>div>p {
    padding: 5rem;
    letter-spacing: 1px;
    word-spacing: 7px;
    line-height: 170%;
    font: 300 1.2rem;
}

.home4 {
    width: 100%;
    height: 100vh;
    background-color: var(--second);
    display: flex;
    justify-content: flex-end;
}

.home4>div {
    background-color: var(--third);
    border-radius: 200px 0 0 200px;
    width: 70%;
    height: 70vh;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.home4>div>h1 {
    color: var(--first);
    text-transform: uppercase;
    width: 140px;
    border-bottom: 2px solid var(--first);
    padding: 0.5rem;
}

.home4>div>article {
    display: flex;
    margin: 2rem 0;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: 12px;
}

.home4>div>article>div {
    animation: imgUp 0.7s linear infinite alternate;
    margin: 1rem;
    height: 10rem;
    width: 10rem;
    padding: 4rem;
    background-color: var(--first);
    border-radius: 50%;
    display: felx;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: var(--dark);
    cursor: pointer;
    transition: all 0.5s;
}

.home4>div>article>div:hover {
    filter: invert(1);
}

.home4>div>article>div>svg {
    font-size: 2rem;
}

.home4>div>article>div>p {
    padding-left: 0px;
    font: 900 1.2rem;
}