@import 'colors';

footer {
    height: 200px;
    width: 100%;
    background-color: var(--first);
    color: var(--dark);
    display: grid;
    grid-template-columns: 4fr 1fr;
    padding: 2rem;
}

footer>div {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

footer>div>h1 {
    font: 700 2rem;
    text-transform: uppercase;
    color: var(--third);
}

footer>div>p {
    font: 500 1.2rem;
}

footer>div>h3 {
    font: 300 5rem;
    margin: 1rem 0;
    text-align: center;
}

footer>div>div {
    display: flex;
    align-items: center;
    flex-direction: column;
}

footer>div>div>a {
    color: var(--dark);
    font: 900 1rem;
}

footer>div>div>a:hover {
    color: var(--third);
}