@import "colors";

nav {
    width: 100%;
    display: flex;
     justify-content: space-between; 
     align-items: center;
     position: sticky;
     top: 0%;
     background-color: var(--first);
     z-index: 10;
     padding: 1rem;
}

nav > h1 {
    text-transform: uppercase;
}

main {
    width:70%;
    display: flex;
    justify-content: flex-end;
}

main > a {
    color: var(--dark);
    margin: 1rem;
}

main > a:hover {
    color: var(--third);
}