@import 'colors';

.contact {
    background-color: var(--second);
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5rem;
}

.contact>main {
    background-color: #fff;
    border-radius: 0 200px 200px 0;
    height: 30rem;
    justify-content: flex-start;
    padding: 3rem;
}


.contact>main>form {
    margin: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.contact>main>form>div {
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 60%;
    margin: 0.5rem;
}

.contact>main>form>div>label {
    width: 20%;
    margin: 1rem;
}

.contact>main>form>div>input {
    align-self: stretch;
    margin: 1rem;
    width: 200px;
    padding: 1rem;
    border-radius: 5px;
    border: 1px solid rgb(123, 123, 123, 0.6);
    outline: none;
}

.contact>main>form>button {
    margin: 1rem;
    padding: 0.7rem;
    border: none;
    border-radius: 5px;
    background-color: var(--third);
    color: var(--first);
    width: 150px;
}

.contact>main>form>button:hover {
    opacity: 0.9;
}